import React, { Component } from 'react'
import baklava from '../baklava.png';
import pangolin from '../pangolin.png';
import joe from '../joe.png'
import Button from 'react-bootstrap/Button'
import ButtonGroup from 'react-bootstrap/ButtonGroup'
import bigInt from 'big-integer'
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';
import { BsFillQuestionCircleFill } from 'react-icons/bs';
import './App.css';


class Deposit extends Component {

  constructor(props) {
    super(props)
    this.state = {
      message: ''
    }
    this.state = {
      txValidAmount: false
    }
    this.state = {
      txDeposit: false
    }
    this.state = {
      txWithdraw: false
    }
    this.clickHandlerDeposit = this.clickHandlerDeposit.bind(this)
    this.clickHandlerWithdraw = this.clickHandlerWithdraw.bind(this)
  }

  changeHandler(event) {
    let result = !isNaN(+event); // true if its a number, false if not
    if (event == "") {
      this.setState({
        message: ''
      })
      this.setState({
        txValidAmount: false
      })
    } else if (result == false) {
      this.setState({
        message: 'Not a valid number'
      })
      this.setState({
        txValidAmount: false
      })
      // console.log(this.state.txValid)
    } else if (event <= 0) {
      this.setState({
        message: 'Value need to be greater than 0'
      })
      this.setState({
        txValidAmount: false
      })
    }
    // else if (bigInt(window.web3Ava.utils.toWei(event, 'ether')).value > bigInt(this.props.lpTokenBalanceAccount[this.props.n][this.props.i]).value) {
    //   this.setState({
    //     message: 'Not enough Balance'
    //   })
    //   this.setState({
    //     txValidAmount: false
    //   })
    // } 
    else {
      this.setState({
        message: ''
      })
      this.setState({
        txValidAmount: true
      })
    }
  }

  clickHandlerDeposit() {
    // console.log("clicked")
    this.setState({
      txDeposit: true,
      txWithdraw: false
    })
  }

  clickHandlerWithdraw() {
    // console.log("clicked")
    this.setState({
      txDeposit: false,
      txWithdraw: true
    })
  }

  render() {
    return (
      <div id="content">
        <div className="text-center">
        </div>

        <form className="mb-3" onSubmit={(event) => {
          event.preventDefault()
          if (this.state.txValidAmount === false) {
            alert("Invalid input! PLease check your input again")
          } else {
            let amount
            amount = this.input.value.toString()
            amount = window.web3Ava.utils.toWei(amount, 'Ether')
            console.log(this.state.txDeposit)
            console.log(this.state.txWithdraw)

            if (this.state.txDeposit === true && this.state.txWithdraw === false) {
              console.log(this.props.n)
              console.log(this.props.i)
              console.log(this.props.lpTokenBalanceAccount)
              if (bigInt(amount).value > bigInt(window.web3Ava.utils.toWei(this.props.lpTokenBalanceAccount[this.props.n][this.props.i], 'Ether')).value) {
                alert("Not enough funds")
              } else {
                this.props.deposit(this.props.i, amount, this.props.n)
              }
            } else if (this.state.txDeposit === false && this.state.txWithdraw === true) {
              if (bigInt(amount).value > window.web3Ava.utils.toWei(this.props.userSegmentInfo[this.props.n][this.props.i], 'Ether')) {
                alert("Withdraw tokens more than deposit LP tokens")
              } else {
                this.props.withdraw(this.props.i, amount, this.props.n)
              }
            }
          }

        }}>
          <div>

            <div className="input-group mb-4">
              <input
                type="text"
                ref={(input) => { this.input = input }}
                className="form-control form-control-lg cardbody"
                placeholder="0"
                onChange={(e) => {
                  const value = e.target.value;
                  // console.log(value)
                  this.changeHandler(value)
                }}
                required />
              <div className="input-group-append">
                <div className="input-group-text cardbody" style={{ color: 'silver' }}>
                  <img src={pangolin} height='25' className="mr-3" alt="" />LP
                </div>
              </div>
            </div >
            <div style={{ color: 'red' }}>{this.state.message} </div>

            <div className="rowC center">
              <ButtonGroup>
                <Button type="submit" variant="primary"
                  size="sm"
                  style={{ minWidth: '90px' }}
                  onClick={(event) => {
                    console.log("clicked deposit...")
                    this.clickHandlerDeposit()
                  }}> Deposit </Button><Button type="text" variant="outline-primary" size="sm" onClick={(event) => {
                    this.input.value = this.props.lpTokenBalanceAccount[this.props.n][this.props.i]
                  }}>All</Button>&nbsp;&nbsp;&nbsp;
              </ButtonGroup>
              <ButtonGroup>
                <Button type="submit" variant="primary"
                  size="sm"
                  style={{ minWidth: '90px' }}
                  onClick={(event) => {
                    console.log("clicked withdraw...")
                    this.clickHandlerWithdraw()
                  }}>Withdraw</Button><Button type="text" variant="outline-primary" size="sm" onClick={(event) => {
                    this.input.value = this.props.userSegmentInfo[this.props.n][this.props.i]
                  }}>All</Button>
              </ButtonGroup>
            </div>
          </div>
        </form>

        <div className="text-center" style={{ color: 'grey' }}><img src={baklava} height='20' alt="" />&nbsp;<small>Contract will automatically harvest BAVA rewards for you when deposit or withdraw token!</small></div>
      </div>

    );
  }
}

export default Deposit;
